<template>
  <div id="goods">
    <van-sidebar v-model="index">
      <van-sidebar-item v-for="(v,i) in getInfo" :key="i" :title="getInfo[i].name" @click="select(i)" :ref="`z${i}`"/>
    </van-sidebar>
    <div class="gright" @scroll="follow()" ref="right">
      <div class="good"  v-for="(v,i) in getInfo" :key="i" :ref="`c${i}`">
        <div class="name">{{v.name}}</div>
        <div class="details" v-for="(j,k) in v.foods" :key="k">
          <div>
            <div class="img">
              <img
                :src="j.image"
              />
            </div>
            <div class="goodtext">
              <p style="font-size:22px;color:#333">{{j.name}}</p>
              <p>{{j.description}}</p>
              <p>月售<span>{{j.sellCount}}</span>份 好评率<span>{{j.rating}}</span>%</p>
              <p class="bottom">
                <span>
                  <i style="color:red;margin-right:10px;font-weight:bold">￥{{j.price}}</i>
                  <del v-if="j.oldPrice">￥{{j.oldPrice}}</del>
                </span>
                <span>
                  <i class="btn" v-show="j.num" @click="$store.commit('add',{n:-1,name:j.name})">-</i>{{j.num > 0 ? j.num : ''}}<i @click="$store.commit('add',{n:1,name:j.name})" class="btn" style="background-color:#1989fa;color:#fff;margin-right:0;margin-left:10px">+</i>
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      data: [],
    };
  },
  methods: {
    select(i){
      let str2 = `this.$refs.c${i}`;
      this.$refs.right.scrollTop = eval(str2)[0].offsetTop - 221;
    },
    follow(){
      for(let i in this.getInfo){
        let top = eval(`this.$refs.c${i}`)[0].offsetTop;
        let h = eval(`this.$refs.c${i}`)[0].offsetHeight;
        let tTop = this.$refs.right.scrollTop;
        if(tTop + 221 >= top && tTop  <= h + top){
          let j = i - 1;
          eval(`this.$refs.z${i}`)[0].$vnode.elm.className = "van-sidebar-item van-sidebar-item--select";
          if(j >= 0){
            eval(`this.$refs.z${j}`)[0].$vnode.elm.className = "van-sidebar-item";
          }
        }
        else{
          eval(`this.$refs.z${i}`)[0].$vnode.elm.className = "van-sidebar-item";
        }
      }
    }
  },
  created() {
    this.$store.dispatch("getGoodsInfo");
  },
  computed : {
    getInfo(){
      let dataList = this.$store.state.data;
      return dataList;
    }
  },
};
</script>

<style>
#goods {
  display: flex;
  justify-content: flex-start;
}
#goods .van-sidebar {
  width: 85px;
  height: 530px;
  margin-bottom: 60px;
  overflow: scroll;
}
#goods .van-sidebar-item--select {
  border-color: #fff;
  background-color: #fff !important;
}
#goods .van-sidebar-item {
  background-color: #f4f5f7;
}
#goods .gright {
  flex: 1;
  height: 530px;
  overflow: scroll;
  margin-left: 0;
  margin-bottom: 60px;
}
#goods .good {
  width: 100%;
}
#goods .good .name {
  line-height: 30px;
  background-color: #f4f5f7;
  border-left: 2px solid #d9dce1;
  text-indent: 1em;
}
#goods .details {
  height: 120px;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}
#goods .details > div {
  box-sizing: border-box;
  height: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
}
#goods .details .img {
  height: 80px;
  width: 80px;
}
#goods .details .img > img {
  width: 100%;
  height: 100%;
}
#goods .details .goodtext {
  flex: 1;
  height: 80px;
  box-sizing: border-box;
  padding-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
#goods .goodtext>p{
  color: #aaa;
  font-size: 14px;
}
#goods .goodtext>p.bottom{
  display: flex;
  justify-content: space-between;
  line-height: 24px;
}
#goods .goodtext>p.bottom .btn{
  display: inline-block;
  height: 20px;
  width: 20px;
  text-align: center;
  border-radius: 50%;
  font-size: 24px;
  line-height: 18px;
  border: 2px solid #1989fa;
  color: #1989fa;
  vertical-align: middle;
  margin-right: 10px;
}
</style>
